import { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import axios from "axios";
import Repos from "../users/Repos";

const User = () => {
  let params = useParams();
  const [data, setData] = useState([]);
  const [repo, setRepo] = useState([]);
  const ress = async () => {
    const repos = await axios(
      `https://api.github.com/users/${params.name}/repos?per_page=5`
    );
    const res = await axios(`https://api.github.com/users/${params.name}`);
    setRepo(repos.data);
    setData(res.data);
  };
  useEffect(() => {
    ress();
  }, []);

  const {
    name,
    avatar_url,
    login,
    location,
    bio,
    blog,
    hireable,
    html_url,
    followers,
    public_repos,
    public_gists,
    following,
  } = data;
  return (
    <div>
      <Link to="/">
        <button className="btn btn-dark m-2">back to search</button>
      </Link>
      <span>
        hiarable :{" "}
        {hireable ? (
          <i className="fa fa-times text-danger"></i>
        ) : (
          <i className=" fa fa-check text-success"></i>
        )}
      </span>
      <div className="user">
        <div className="section">
          <img src={avatar_url} alt="picture" />
          <p>{name}</p>
          <p>{location}</p>
        </div>
        <div className="section">
          <p>BIO</p>
          <p>{bio}</p>
          <a href={html_url} target="_blank">
            <button className="btn btn-dark ">Visit Github Page </button>
          </a>
          <p>Login : {login}</p>
          <p>Website : {blog}</p>
        </div>
      </div>
      <div className="follow">
        <button className="btn btn-danger">Followers : {followers}</button>
        <button className="btn btn-dark">Following : {following}</button>
        <button className="btn btn-warning">
          Public Repost:{public_repos}
        </button>
        <button className="btn btn-success">
          Public gists :{public_gists}
        </button>
      </div>

      <Repos repo={repo}></Repos>
    </div>
  );
};

export default User;
