import { useContext } from "react";
import GithubContex from "../../contex/githubContex";

const Spineer = () => {
  const { loader } = useContext(GithubContex);

  switch (loader) {
    case true:
      return <div className="spineer"></div>;
    case false:
      return null;

    default:
      return null;
  }
};

export default Spineer;
