import React from "react";

const Repos = ({ repo }) => {
  return (
    <ul className="list-group">
      {repo.map((rep) => (
        <li key={rep.id} className="list-group-item">
          <a style={{ color: "black" }}>{rep.html_url}</a>
        </li>
      ))}
    </ul>
  );
};

export default Repos;
