import { useContext } from "react";
import SingleUser from "./SingleUser";
import GithubContex from "../../contex/githubContex";

const Users = () => {
  const { users } = useContext(GithubContex);
  return (
    <div className="row">
      {users.map((user) => {
        return <SingleUser key={user.id} user={user} />;
      })}
    </div>
  );
};

export default Users;
