import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <nav className="bg-danger text-white p-4">
      <div className="logo">
        <i className="fab fa-github me-2"></i>
        GITHUB FINDER
      </div>
      <ul>
        <li>
          <Link to="/">HOME</Link>
        </li>
        <li>
          <Link to="/about">ABOUT</Link>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
