import { useState, useContext } from "react";
import GithubContex from "../../contex/githubContex";
const Search = () => {
  const { clearUser, search, users } = useContext(GithubContex);
  const [text, setText] = useState("");

  const formSubmit = (e) => {
    e.preventDefault();
    if (text.trim() !== "") {
      search(text);
      setText("");
    }
  };
  return (
    <form onSubmit={formSubmit} className="mb-3 mt-3">
      <input
        className="form-control mb-3 "
        type="text"
        value={text}
        onChange={(e) => setText(e.target.value)}
      />
      <button className="btn btn-primary text-white w-100 mb-3">sarch</button>
      {users.length > 0 && (
        <button onClick={clearUser} className="btn btn-info text-white w-100">
          clear
        </button>
      )}
    </form>
  );
};

export default Search;
